<template>
  <div>
    <div class="info__container">
      <div class="info__checkbox info__wrapper">
        <span class="modal-window__terms">
          <app-checkbox
            v-model="checkBoxStatus"
            :value="status"
            :val="status"
            :checkboxId="status"
            :checkBoxText='status'
            textStyle='grey'
            v-for="status in statuses"
            :key="status"
          ></app-checkbox>
          <!-- {{checkBoxStatus}} -->
        </span>
      </div>
      <div class="info__wrapper" v-if="activeMarker">
        <p v-if="activeMarker.contractor">Підрядник: {{activeMarker.contractor}}</p>
        <p v-if="activeMarker.date">Дата проведення роботи: {{activeMarker.date}}</p>
        <p v-if="activeMarker.orderIdentifier">Номер замовлення: {{activeMarker.orderIdentifier}}</p>
        <p v-if="activeMarker.sender">Замовник: {{activeMarker.sender}}</p>
        <p v-if="activeMarker.status">Статус виконання: {{activeMarker.status}}</p>
        <p v-if="activeMarker.type">Дерево: {{activeMarker.type}}</p>
      </div>
    </div>
  </div>
</template>

<script>
// import Map from '@/components/Map'
import Checkbox from '@/components/ui/Checkbox.vue'
import {mapState, mapActions} from 'vuex';

export default {
  data() {
    return {
      statuses: ["Нові дереві", "Обрізані дерева", "Видалені дерева"],
      checkBoxStatus: ["Нові дереві", "Обрізані дерева", "Видалені дерева"],
      showPrudesTrees: true,
      showNewTrees: true,
      showRemovedTrees: true
    }
  },
  components: {
    'app-checkbox': Checkbox
  },
  computed:
    mapState({
      activeMarker({ecology}) {
        return ecology.activeMarkerEcology
      },
    }),
  created() {
    console.log('created eco')
  },
  mounted() {
    console.log('mounted eco')
  },
  beforeDestroy() {
    this.addActiveMarkerEcology({})
    this.filterEcology(["Нові дереві", "Обрізані дерева", "Видалені дерева"])
  },
  methods: {
    ...mapActions([
      'filterEcology',
      'addActiveMarkerEcology'
    ]),
  },
  watch: {
    checkBoxStatus: function (val) {
      this.filterEcology(val)
    }
  },
}
</script>

<style lang="scss" scoped>
.info {
  &__wrapper {
    padding: 50px;
  }
  &__checkbox {
    display: flex;
    width: max-content;
  }
  &__container {
    display: flex;
    flex-wrap: wrap;
  }
}
</style>