<template>
  <div class="checkbox-wrapper" >
    <input
      type="checkbox"
      v-model="checked"
      class="checkbox"
      :value="checkboxId"
      :id="checkboxId"
      @change="onChange"
    >
    <label class="label" :for="checkboxId"></label> 
    <span class="checkbox__text" :class="textStyle">
      {{checkBoxText}}
    </span>
    
  </div>
</template>

<script>
export default {
  props: ["value", "val", "checkboxId", "checkBoxText", "textStyle"],
  data() {
    return {
      checkedProxy: false
    };
  },
  computed: {
    checked: {
      get() {
        return this.value;
      },
      set(val) {
        this.checkedProxy = val;
      }
    }
  },
  methods: {
    onChange: function() {
      this.$emit("input", this.checkedProxy);
    },
  },
};
</script>

<style lang="scss" scoped>
.label {
  display: block;
  cursor: pointer;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.04);
  border: solid 0.5px #48484633;
  background-color: #ffffff;
  position: relative;
  transition: 0.3s;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  margin-right: 5px;
  
}

.checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.05px;
  }
  &-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  &:checked + .label {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.04);
    border: solid 2px #e7b4b4;
  }
}
.checkbox:checked ~ .label {
  transition: 0.3s;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.04);
  border: solid 0.5px #e7b4b4;
  position: relative;
}
.checkbox:checked ~ .label:after {
  transition: 0.3s;
  content: url(../../assets/Checkbox.png);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}

.grey {
  color: #9EA0A5;
  a {
    color: black;
    
  }
}
</style>

